.header-derecha {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet {
        align-items: flex-end;
    }
}
.boton-darkMode {
    width: 3rem;    
    margin: 2rem 0;
    filter: invert(100%);
    @include tablet {
        margin-top: -1rem;
        margin-bottom: 4rem;
    }
    &:hover {
        cursor: pointer;
    }
}

.barra {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
    align-items: center;
}
.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear, height 0.5s linear;

    @include tablet {
        display: flex; 
        align-items: flex-end; 
        opacity: 1; 
        visibility: visible; 
    }
    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        &:hover {
            color: $amarillo;
        }
        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}
/**Menu de hamburguesa**/
.hamburguesa {
    @include tablet {
        display: none;
    }
    img {
        width: 5rem;
        margin: 1rem 0 ;
    }
}

.mostrar-nav {
    visibility: visible;
    opacity: 1;
    height: auto;
}