.contenedor-nosotros {
    @include tablet {
        @include grid(2,2rem);
    }
}
.nosotros-contenido {
    blockquote {
        font-weight: bold;
        margin: 0;
        font-size: 2.2rem;
        padding: 1rem 0 3rem 0;
        margin-top: 2rem;
        @include tablet {
            margin-top: 0;
        }
    }
}