.testimonial {
    background-color: $verde;    
    font-size: 2rem;
    border-radius: 1.5rem;
    color: $blanco;
    font-weight: bold;
    padding: 2rem;
    position: relative;
    blockquote {
        font-weight: normal;
        padding-left: 5rem;
        &::before {
            content: '';
            background-image: url(../img/comilla.svg);
            display: block;
            width: 6rem;
            height: 8rem;
            background-repeat: no-repeat;
            position: absolute;
            left: 4rem;
        }
    }
    p {
        color: $blanco;
        text-align: end;
    }
}