.dark-mode {
    background-color: darken($color: $grisOscuro, $amount: 10);

    p {
        color: $blanco;
    }
    h1, h2, h3, h4 {
        color: $gris;
    }
    .anuncio {
        .contenido-anuncio {
            background-color: $grisOscuro;
            border: 1px solid $grisOscuro;
        }
        .precio {
            color: $verde;
        }
    }
    .iconos-caracteristicas {
        img {
            filter: invert(100%);
        }
    }
    .nosotros-contenido {
        blockquote {
            color: $blanco;
        }
    }
    .resumen-propiedad {
        .precio {
            color: $verde;        
        }
    }
    .formulario {
        legend, label, p {
            color: $gris;
        }
        input:not([type="submit"]), textarea, select {
            background-color: $grisOscuro;
            border: 1px solid $grisOscuro;
            color: $blanco;
        }
    }
}