.alerta {
    padding: 0.5rem;
    text-align: center;
    color: $blanco;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error {
        background-color: $rojo;
    }
    &.confirmacion {
        background-color: $verde;
        padding: 2rem;
        font-size: 2.5rem;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $verde;
        
        th {
            color: $blanco;
            padding: 2rem;
            text-align: center;
        }
    }
    tbody {
        background-color: $gris;
        tr {
            text-align: center;
            &:nth-child(odd) {
                background-color: $blanco;
            }
        }        
    }
    .imagen-tabla {
        width: 15rem;
    }
}
.imagen-small {
    width: 25rem;
}