@include tablet {
    .blogtestimoniales {
        display: grid;
        grid-template-columns: 2fr 1fr;
        gap: 2rem;
    }
}
.blog-entrada {
    margin-bottom: 2rem;
    &:last-of-type {
        margin-bottom: 0;
    }
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
}
.entrada-texto {
    a {
        color: $negro;
        margin-top: 3rem;
        display: block;
        @include tablet {
            margin-top: 0;
        }

    }
    h4 {
        margin: 0;
        line-height: 1.4;
        &::after {
            content: '';
            display: block;
            background-color: $verde;
            height: 0.5rem;
            width: 15rem;
            margin-top: 1rem;
        }
    }
}
.informacion-autor {
    span {
        color: $amarillo;
    }
}
.boton-blog {
    margin-top: 0;
}