.contenedor-anuncios {
    @include tablet {
        @include grid(3,2rem);
    }
    .anuncio {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: lighten($color: $gris, $amount: 5);
        border: 1px solid darken($color: $gris, $amount: 5);
        margin-bottom: 3rem;
        &:last-of-type {
            margin-bottom: 0;
        }
        @include tablet {
            margin-bottom: 0;
        }
    }
}
.contenido-anuncio {
    padding: 2rem;
    h3,p {
        margin: 0;
    }
    h3 {
        font-weight: bold;
        margin-top: 2rem;
    }
    p {
        text-align: justify;
    }
}
.precio {
    color: $verde;
    font-weight: bold;
    font-size: 3rem;
}
.iconos-caracteristicas {
    list-style: none;
    padding: 0;
    display: flex;
    li {
        flex: 1;
        display: flex;
        justify-content: center;
        img {            
            flex: 0 0 3rem;
            margin-right: 1rem;
        }
        p {
            font-weight: $black;
        }
    }
}