.no-webp .inicio {
    background-image: url('../img/header.jpg');
}
.webp .inicio {
    background-image: url('../img/header.webp');
}
.header {
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;
    &.inicio {
        background-position: center center;
        background-size: cover;
        .contenido-header {
            @include tablet {
                min-height: 47rem;
            }
        }
    }
}
.contenido-header {
    color: $blanco;
    display: flex;
    flex-direction: column;
    
    @include tablet {
        justify-content: space-between;    
    }
    h1 {
        text-align: center;
        font-weight: $bold;
        font-size: 3rem;
        line-height: 2;
         //Agregarle un salto de línea
        @include tablet {
            text-align: left;
            max-width: 55rem;
        }
    }
}