.footer {
    background-color: $grisOscuro;
    padding: 1rem 0;
    margin-bottom: 0;
    .contenido-footer {
        padding-top: $separacion / 2;
        display: flex;
        justify-content: center;
        @include tablet {
            justify-content: right;
        }
    }
    .navegacion {
        visibility: visible;
        opacity: 1;
        height: auto;
    }
    .copyright {
        color: $blanco;
        text-align: center;
    }
}