.no-webp .contacto {
    background-image: url(../img/encuentra.jpg);
}
.webp .contacto {
    background-image: url(../img/encuentra.webp);
}

.contacto {
    background-position: center center;
    background-size: cover;
    padding: $separacion+1 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet {
        min-height: 30rem;
    }
    h3 {
        font-size: 4rem;
        color: $blanco;
        font-weight: bold;
    }
    p {
        font-size: 1.8rem;
        color: $blanco;
        text-align: center;
    }
    h3,p {
        margin: 1rem;
    }
}